@import url('https://fonts.googleapis.com/css2?family=Hahmlet:wght@600;700&display=swap');


$mainColor : #313131;
$subColor : #88888888;
$pointColor : #fff34c;



@mixin opacity($num) {
  opacity: $num;
}


.fadeIn {
  opacity: 1;
}

.fadeOut {
  opacity: 0;
}

// nav css

.container {
  padding: 0 90px;
  margin-top: 20px;
}

.navBox {
  width: 100%;
  height: 48px;
  padding-right: 90px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  position: fixed;
  position: relative;
  top: 0;
  border-bottom: 0.5px solid $subColor;
  backdrop-filter: saturate(180%) blur(20px);
  background-color: rgba(255,255,255,0.72);
}

.logoImg {
  margin-left: auto;
  width: 30px;
  height: 30px;
  background-image: url('./logo.png');
  background-size: contain;
  background-position: center;
  @include opacity(0.6);

  &:hover {
    @extend .fadeIn;
  }
}

.list {
  position: absolute;
  top: 48px;
  right: 80px;
  list-style: none;
  letter-spacing: 1px;
  font-size: 15px;
  transition: opacity 0.5s;
  margin-top: 8px;
 
  a {
    text-decoration: none;
  }
  
  li {
    color: $mainColor;
    margin-bottom: 5px;
    padding-bottom: 2px;
    border-bottom: 0.5px solid $subColor;
    @include opacity(0.6);

    &:hover {
      cursor: pointer;
      @extend .fadeIn;
    }
  }
}

// about css

.textContainer {
  font-family: 'Hahmlet', serif;
  margin-top: 100px;
  color: $mainColor;
  font-weight: 700;
  font-size: 70px;

  p {
    margin: 0;
    transition: opacity 0.5s;
  }

}

.mainText {
  margin-top: 70px;
  line-height : 1.5;
  padding: 0 5px;
  font-size: 17px;
  font-weight: 300;
  transition: opacity 0.5s;

  span {
    font-family: 'Hahmlet', serif;
    font-weight: 400;
  }
}


//project

.marginTop {
  margin-top: 100px;
}

.projectContainer {
  display: flex;
  flex-direction: row-reverse;
  justify-content: space-between;
  border-bottom: 1px solid $subColor;
  padding-bottom: 50px;
  margin-bottom: 80px;
  color: $mainColor;

  img {
    display: block;
    margin: auto;
    max-width: 550px;
  }
  
}

.AboutProject {
  margin-right: 30px;

  h1 {
    font-size: 38px;
    font-weight: 700;
    margin-top: 0px;
  }

  h4 {
    margin-top: 30px;
    margin-bottom: 0px;
    font-weight: 500;
  }

  span {
    font-size: 12px;
    opacity: 0.8;
  }
}

.btn {
  width: 80px;
  height: 30px;
  border-radius: 30px;
  border: 1px solid $mainColor;
  background-color: #fff;
  margin-top: 30px;
  margin-right: 12px;
  

  a {
    color: $mainColor;
    text-decoration: none;
  }

  &:hover {
    background-color: $mainColor;
    transition: all 0.3s;
    a {
      color: #fff;
    }
    
  }
}



@media screen and (max-width : 1054px) {
  //project
  .AboutProject {
    margin-right: 0px;
  }
  .projectContainer {
    display: flex;
    flex-direction: column;

    img {
      margin-bottom: 20px
    }
  }
};


@media screen and (max-width : 768px) {
  .navBox {
    padding-right: 40px;
  }

  .list {
    right: 30px;
  }
  .container {
    padding: 0 40px;
  }

  //about

  .textContainer {
    font-weight: 700;
    font-size: 50px;
  }

  .AboutProject {
    h1 {
      font-size: 30px;
    }
  }
}